import backend from "../../api/backend";
import { CLOSE_LOADER, SET_LOADER } from "../Types/AuthTypes";
import {
  SET_CONTENT,
  SET_FILES,
  SET_ERROR,
  SET_SUCCESS,
} from "../Types/ConverterTypes";

export const addFiles = (userId, data, progressCallback) => {
  return async (dispatch) => {
    dispatch({ type: SET_LOADER });
    try {
      const response = await backend.post(`/upload/${userId}/`, data, {
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
          progressCallback(percentCompleted);
        },
      });
      dispatch({ type: CLOSE_LOADER });
      dispatch({ type: SET_FILES, payLoad: response.data });
      return true;
    } catch (err) {
      dispatch({ type: CLOSE_LOADER });
      dispatch({
        type: SET_ERROR,
        payLoad:
          err.response?.data?.error || "An error occurred during file upload",
      });
      return false;
    }
  };
};

export const addGuide = (data) => {
  return async (dispatch) => {
    dispatch({ type: SET_LOADER });
    try {
      const response = await backend.post("/guides/save/", data);
      dispatch({ type: CLOSE_LOADER });

      dispatch({ type: SET_SUCCESS, payLoad: response.data.success.msg });

      return true;
    } catch (err) {
      dispatch({ type: CLOSE_LOADER });
      console.log(err);
      dispatch({
        type: SET_ERROR,
        payLoad: err.response?.data?.error?.msg,
      });
      return false;
    }
  };
};

export const getFiles = (userId) => {
  return async (dispatch) => {
    dispatch({ type: SET_LOADER });
    try {
      const response = await backend.get(`/user/${userId}/files/`);
      dispatch({ type: CLOSE_LOADER });
      if (Array.isArray(response.data)) {
        dispatch({
          type: SET_FILES,
          payLoad: response.data,
        });
      } else {
        throw new Error("Invalid response format: expected an array");
      }
    } catch (err) {
      dispatch({ type: CLOSE_LOADER });
      dispatch({
        type: SET_ERROR,
        payLoad:
          err.response?.data?.error || "An error occurred while fetching files",
      });
    }
  };
};

export const processPpt = (fileId) => {
  return async (dispatch) => {
    dispatch({ type: SET_LOADER });
    try {
      const response = await backend.get(`/process-pptx/${fileId}/`);

      dispatch({ type: CLOSE_LOADER });

      dispatch({ type: SET_CONTENT, payLoad: response.data });
    } catch (err) {
      dispatch({ type: CLOSE_LOADER });
      console.log(err);
      dispatch({
        type: SET_ERROR,
        payLoad: err.response?.data?.error?.msg,
      });
    }
  };
};
