import {
  RESET_CONTENT,
  SET_CONTENT,
  SET_FILES,
  SET_ERROR,
  CLEAR_ERROR,
} from "../Types/ConverterTypes";

const initialState = {
  files: [],
  content: null,
  error: null,
};

const ConverterReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_FILES:
      return { ...state, files: action.payLoad, error: null };
    case SET_CONTENT:
      return { ...state, content: action.payLoad, error: null };
    case RESET_CONTENT:
      return { ...state, content: null };
    case SET_ERROR:
      return { ...state, error: action.payLoad };
    case CLEAR_ERROR:
      return { ...state, error: null };
    default:
      return state;
  }
};

export default ConverterReducer;
