import React, { Fragment, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { logout } from "../store/AsyncMethods/AuthMethod";

const Navbar = ({ logout, isAuthenticated }) => {
  const [redirect, setRedirect] = useState(false);

  const logout_user = () => {
    logout();
    setRedirect(true);
  };

  const guestLinks = () => (
    <Fragment>
      <li className="nav-item">
        <Link
          className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
          to="/login"
        >
          Login
        </Link>
      </li>
      <li className="nav-item ml-4">
        <Link
          className="px-4 py-2 bg-green-500 text-white rounded hover:bg-green-600"
          to="/signup"
        >
          Sign Up
        </Link>
      </li>
    </Fragment>
  );

  const authLinks = () => (
    <li className="nav-item">
      <a
        className="px-4 py-2 text-white bg-red-500 rounded cursor-pointer hover:bg-red-600"
        href="#!"
        onClick={logout_user}
      >
        Logout
      </a>
    </li>
  );

  return (
    <Fragment>
      <nav className="bg-gray-800 p-4">
        <div className="container mx-auto flex justify-between items-center">
          <Link className="text-white text-lg font-bold" to="/">
            <span className="text-blue-500">PPT</span>HyperLink
            <span className="text-blue-500">Detector</span>
          </Link>
          <ul className="flex items-center space-x-6 text-white">
            <li>
              <Link className="hover:text-blue-400" to="/">
                Home
              </Link>
            </li>
            <li>
              <Link className="hover:text-blue-400" to="/uploads">
                Uploads
              </Link>
            </li>
            <li>
              <Link className="hover:text-blue-400" to="/converter">
                Converter
              </Link>
            </li>
            <li>
              <Link className="hover:text-blue-400" to="/contact">
                Contact
              </Link>
            </li>
            {isAuthenticated ? authLinks() : guestLinks()}
          </ul>
        </div>
      </nav>
      {/* {redirect ? <Redirect to="/" /> : <Fragment></Fragment>} */}
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.AuthReducer.isAuthenticated,
});

export default connect(mapStateToProps, { logout })(Navbar);
