import React, { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { processPpt, getFiles } from "../store/AsyncMethods/ConverterMethod";
import DocViewer, { DocViewerRenderers } from "@cyntler/react-doc-viewer";
import "@cyntler/react-doc-viewer/dist/index.css";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function Converter() {
  const [file, setFile] = useState(null);
  const { content } = useSelector((state) => state.ConverterReducer);
  const { isAuthenticated, user } = useSelector((state) => state.AuthReducer);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const location = useLocation();
  const [fileUrl, setFileUrl] = useState([]);
  const [docViewerHeight, setDocViewerHeight] = useState("calc(100vh - 200px)");

  useEffect(() => {
    if (location.state && location.state.selectedFile) {
      setFile(location.state.selectedFile);
      dispatch(processPpt(location.state.selectedFile?.id))
        .then(() => toast.success("File processed successfully!"))
        .catch(() => toast.error("Error processing file."));
    }
  }, [location, dispatch]);

  useEffect(() => {
    if (content) {
      const url = content.file_url.Url;
      const docsx = [
        {
          uri: url,
          fileType: "pptx",
        },
      ];
      setFileUrl(docsx);
    }
  }, [content]);

  useEffect(() => {
    const calculateHeight = () => {
      const navbarHeight = document.querySelector("nav")?.offsetHeight || 0;
      const containerPadding = 32;
      const availableHeight =
        window.innerHeight - navbarHeight - containerPadding;
      setDocViewerHeight(`${availableHeight}px`);
    };

    calculateHeight();
    window.addEventListener("resize", calculateHeight);

    return () => window.removeEventListener("resize", calculateHeight);
  }, []);

  useEffect(() => {
    if (!isAuthenticated || !user) {
      setFile(null);
      navigate("/uploads");
    }
  }, [isAuthenticated, user, navigate]);

  const handleSelectFile = () => {
    navigate("/uploads");
  };

  const handleReset = () => {
    setFile(null);
    navigate("/uploads");
  };

  if (!isAuthenticated || !user) {
    return (
      <div className="flex-grow container mx-auto py-12 px-4 md:px-8">
        <h1 className="text-4xl font-bold text-center text-blue-600 mb-12">
          Please login first
        </h1>
      </div>
    );
  }

  return (
    <div className="overflow-hidden">
      <ToastContainer />
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-3xl font-bold mb-8 text-center">
          PPT HyperLink Detector
        </h1>
        <div className="bg-white shadow-md rounded px-8 pt-6 pb-8 relative">
          <div className="mb-6 flex items-center justify-between">
            <div className="flex items-center">
              <button
                onClick={handleSelectFile}
                className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline flex items-center"
              >
                <svg
                  className="w-5 h-5 mr-2"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                >
                  <path
                    fillRule="evenodd"
                    d="M10 3a1 1 0 011 1v5h5a1 1 0 110 2h-5v5a1 1 0 11-2 0v-5H4a1 1 0 110-2h5V4a1 1 0 011-1z"
                    clipRule="evenodd"
                  />
                </svg>
                Select PPTx
              </button>
              {file && <span className="ml-4">{file.name}</span>}
            </div>
          </div>

          <div className="flex-grow overflow-hidden">
            <DocViewer
              documents={fileUrl}
              pluginRenderers={DocViewerRenderers}
              style={{ height: docViewerHeight }}
              config={{
                header: {
                  disableHeader: false,
                  disableFileName: false,
                  retainURLParams: false,
                },
              }}
            />
          </div>
          <button
            onClick={handleReset}
            className="mt-4 bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
          >
            Reset
          </button>
        </div>
      </div>
    </div>
  );
}

export default Converter;
