import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { addFiles, getFiles } from "../store/AsyncMethods/ConverterMethod";
import { RESET_CONTENT } from "../store/Types/ConverterTypes";
import backend from "../api/backend";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Modal from "react-modal";

Modal.setAppElement("#root"); // For accessibility

function Uploads() {
  const navigate = useNavigate();
  const { user } = useSelector((state) => state.AuthReducer);
  const { files, error } = useSelector((state) => state.ConverterReducer);
  const dispatch = useDispatch();

  const [uploadProgress, setUploadProgress] = useState(0);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedFileId, setSelectedFileId] = useState(null);

  useEffect(() => {
    if (user) {
      dispatch(getFiles(user.id));
    }
  }, [dispatch, user]);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      const formData = new FormData();
      formData.append("file", file);
      dispatch(addFiles(user.id, formData, setUploadProgress))
        .then((success) => {
          if (success) {
            dispatch(getFiles(user.id));
            toast.success("File uploaded successfully!");
          }
        })
        .catch(() => {
          toast.error("Failed to upload file.");
        });
    }
  };

  const openModal = (fileId) => {
    setSelectedFileId(fileId);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setSelectedFileId(null);
    setModalIsOpen(false);
  };

  const handleDel = async () => {
    if (selectedFileId) {
      try {
        await backend.post(`/user/${user.id}/files/${selectedFileId}/delete/`);
        dispatch(getFiles(user.id)); // Refresh the file list
        toast.success("File deleted successfully!");
      } catch (error) {
        console.error("Error deleting file:", error);
        toast.error("Failed to delete file.");
      }
      closeModal();
    }
  };

  const handleConvert = (file) => {
    dispatch({ type: RESET_CONTENT });
    navigate("/converter", { state: { selectedFile: file } });
  };

  if (!user) {
    return (
      <div className="flex-grow container mx-auto py-12 px-4 md:px-8">
        <h1 className="text-4xl font-bold text-center text-blue-600 mb-12">
          Please login first
        </h1>
      </div>
    );
  }

  return (
    <div className="flex-grow container mx-auto py-12 px-4 md:px-8">
      <h1 className="text-4xl font-bold text-center text-blue-600 mb-12">
        Uploaded PPT Files
      </h1>
      {error && (
        <div
          className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative mb-4"
          role="alert"
        >
          <strong className="font-bold">Error:</strong>
          <span className="block sm:inline"> {error}</span>
        </div>
      )}
      <div className="mb-12">
        <div className="flex items-center justify-center">
          <input
            type="file"
            accept=".ppt,.pptx"
            onChange={handleFileUpload}
            className="block w-full max-w-lg text-sm text-gray-500
                       file:mr-4 file:py-3 file:px-6
                       file:rounded-full file:border-0
                       file:text-sm file:font-semibold
                       file:bg-blue-100 file:text-blue-700
                       hover:file:bg-blue-200 transition-colors duration-300"
          />
        </div>
        {uploadProgress > 0 && uploadProgress < 100 && (
          <div className="mt-4 w-full max-w-lg mx-auto">
            <div className="relative pt-1">
              <div className="overflow-hidden h-6 mb-4 text-xs flex rounded bg-green-200">
                <div
                  style={{ width: `${uploadProgress}%` }}
                  className="shadow-none flex flex-col text-center whitespace-nowrap text-white justify-center bg-green-500 transition-all duration-300"
                ></div>
              </div>
            </div>
          </div>
        )}
      </div>
      <div className="bg-white shadow-lg rounded-lg overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-blue-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                File Name
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Upload Date
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Action
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {Array.isArray(files) && files.length > 0 ? (
              files.map((file) => (
                <tr
                  key={file.id}
                  className="hover:bg-gray-100 transition-colors"
                >
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm font-medium text-gray-900">
                      {file.file_name}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap">
                    <div className="text-sm text-gray-500">
                      {file.upload_date}
                    </div>
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap flex gap-2">
                    <button
                      onClick={() => handleConvert(file)}
                      className="bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-4 rounded transition-colors duration-300"
                    >
                      Convert
                    </button>
                    <button
                      onClick={() => openModal(file.id)}
                      className="bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded transition-colors duration-300"
                    >
                      Delete
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="4" className="text-center py-4">
                  No files available
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
      {/* Toast Notifications */}
      <ToastContainer />
      {/* Delete Confirmation Modal */}
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Confirm Delete"
        className="bg-white p-6 rounded-lg shadow-lg max-w-sm mx-auto"
        overlayClassName="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center"
      >
        <h2 className="text-lg font-bold mb-4">Confirm Delete</h2>
        <p className="mb-4">Are you sure you want to delete this file?</p>
        <div className="flex justify-end gap-4">
          <button
            onClick={handleDel}
            className="bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded transition-colors duration-300"
          >
            Yes, Delete
          </button>
          <button
            onClick={closeModal}
            className="bg-gray-300 hover:bg-gray-400 text-black font-bold py-2 px-4 rounded transition-colors duration-300"
          >
            Cancel
          </button>
        </div>
      </Modal>
    </div>
  );
}

export default Uploads;
