import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { connect } from "react-redux";
import { signup } from "../store/AsyncMethods/AuthMethod";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Signup = ({ signup, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    password: "",
    re_password: "",
  });

  const navigate = useNavigate();
  const { first_name, last_name, email, password, re_password } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async (e) => {
    e.preventDefault();

    if (password !== re_password) {
      toast.error("Passwords do not match.");
      return;
    }

    try {
      await signup(first_name, last_name, email, password, re_password);
      toast.success("Verification email sent! Please check your inbox.");
      setTimeout(() => navigate("/login"), 3000); // Navigate to login page after 3 seconds
    } catch (error) {
      toast.error("Failed to create account. Please try again.");
    }
  };

  if (isAuthenticated) {
    navigate("/");
  }

  return (
    <div className="flex items-center justify-center min-h-screen bg-blue-50">
      <div className="bg-white shadow-lg rounded-lg p-8 max-w-md w-full">
        <h1 className="text-2xl font-bold text-blue-600 mb-4">Sign Up</h1>
        <p className="text-gray-600 mb-6">Create your Account</p>
        <form onSubmit={(e) => onSubmit(e)}>
          <div className="mb-4">
            <input
              className="w-full px-4 py-2 border border-blue-300 rounded-md text-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent"
              type="text"
              placeholder="First Name*"
              name="first_name"
              value={first_name}
              onChange={(e) => onChange(e)}
              required
            />
          </div>
          <div className="mb-4">
            <input
              className="w-full px-4 py-2 border border-blue-300 rounded-md text-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent"
              type="text"
              placeholder="Last Name*"
              name="last_name"
              value={last_name}
              onChange={(e) => onChange(e)}
              required
            />
          </div>
          <div className="mb-4">
            <input
              className="w-full px-4 py-2 border border-blue-300 rounded-md text-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent"
              type="email"
              placeholder="Email*"
              name="email"
              value={email}
              onChange={(e) => onChange(e)}
              required
            />
          </div>
          <div className="mb-4">
            <input
              className="w-full px-4 py-2 border border-blue-300 rounded-md text-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent"
              type="password"
              placeholder="Password*"
              name="password"
              value={password}
              onChange={(e) => onChange(e)}
              minLength="6"
              required
            />
          </div>
          <div className="mb-4">
            <input
              className="w-full px-4 py-2 border border-blue-300 rounded-md text-gray-700 focus:outline-none focus:ring-2 focus:ring-blue-600 focus:border-transparent"
              type="password"
              placeholder="Confirm Password*"
              name="re_password"
              value={re_password}
              onChange={(e) => onChange(e)}
              minLength="6"
              required
            />
          </div>
          <button
            className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 transition duration-200"
            type="submit"
          >
            Register
          </button>
        </form>
        <p className="mt-4 text-center text-gray-600">
          Already have an account?{" "}
          <Link to="/login" className="text-blue-600 hover:underline">
            Sign In
          </Link>
        </p>
      </div>
      <ToastContainer />
    </div>
  );
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.AuthReducer.isAuthenticated,
});

export default connect(mapStateToProps, { signup })(Signup);
