import React from "react";
import Navbar from "../Components/Navbar";
import Footer from "../Components/Footer";

function Admin() {
  const users = [
    { id: 1, name: "John Doe", email: "john@example.com", role: "User" },
    { id: 2, name: "Jane Smith", email: "jane@example.com", role: "Admin" },
    // Add more users as needed
  ];

  return (
    <>
      <div className="p-8 bg-gray-100 maz-h-screen">
        <div className="bg-white shadow-md rounded-lg overflow-hidden">
          <div className="px-6 py-4 bg-gray-800 text-white">
            <h1 className="text-2xl font-semibold">Manage Users</h1>
          </div>
          <div className="p-6">
            <table className="min-w-full bg-white">
              <thead>
                <tr>
                  <th className="py-2 px-4 border-b-2 border-gray-300 text-left leading-4">
                    ID
                  </th>
                  <th className="py-2 px-4 border-b-2 border-gray-300 text-left leading-4">
                    Name
                  </th>
                  <th className="py-2 px-4 border-b-2 border-gray-300 text-left leading-4">
                    Email
                  </th>
                  <th className="py-2 px-4 border-b-2 border-gray-300 text-left leading-4">
                    Role
                  </th>
                  <th className="py-2 px-4 border-b-2 border-gray-300 text-left leading-4">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                {users.map((user) => (
                  <tr key={user.id}>
                    <td className="py-2 px-4 border-b border-gray-300">
                      {user.id}
                    </td>
                    <td className="py-2 px-4 border-b border-gray-300">
                      {user.name}
                    </td>
                    <td className="py-2 px-4 border-b border-gray-300">
                      {user.email}
                    </td>
                    <td className="py-2 px-4 border-b border-gray-300">
                      {user.role}
                    </td>
                    <td className="py-2 px-4 border-b border-gray-300">
                      <button className="bg-blue-500 text-white px-4 py-1 rounded mr-2 hover:bg-blue-700">
                        Edit
                      </button>
                      <button className="bg-red-500 text-white px-4 py-1 rounded hover:bg-red-700">
                        Delete
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}

export default Admin;
